// This is the Link API
import Link from 'next/link';
import Header from '../components/Header';

const Index = () => (
  <div>
    <Header />
    <p>Hello Next.js</p>
    <Link href="/about"><a style={{ fontSize: 20 }} href="javascript:;">About Page</a></Link>
  </div>
);

export default Index;



